.experiance__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.experiance__container > div {
 background-color: var(--color-bg-variant);
 padding: 2.2rem, 5rem;
 border-radius: 1rem;
 border: 1px solid transparent;   
 transform: var(--transition);
}

.experiance__container > div:hover {
    background: transparent;
    background-color: var(--color-primary-variant);
    cursor: default;
}

.experiance__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experiance__content {
    display: grid;
    grid-template-columns: 1.5fr 1.5fr;
    row-gap: 1rem;
}

.experiance__details {
    display: flex;
    gap: 1rem;
}

.experiance__details-icon {
    margin-top: 6px;
    color: var(--color-primary);
}

/* ================== MEDIA QUERIES (MEDIUM DEVICES)============== */
@media screen and (max-width: 1024px) {
    .experiance__container{
        grid-template-columns: 1fr;

    }

    .experiance__container > div {
        width: 80%;
        padding: 2 rem;
        margin: 0 auto;
    }

    .experiance__content {
        padding: 1rem;
    }
}

/* ================== MEDIA QUERIES (SMALL DEVICES)============== */
@media screen and (max-width: 600px) {
    .experiance__container{
        gap: 1rem;

    }

    .experiance__container > div {
        width: 80%;
        padding: 2 rem 1rem;
    }
}